import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import details from "../views/details";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/details/:name',
    name: 'details',
    component: details
  },
]

const router = new VueRouter({
  routes
})

export default router